import { BaseCommand } from './BaseCommand';
import { ChangeCardAtkDefCommandData } from '../types/commands';
import { YGODuelEvents } from '../types/duel-events';
import { YGOUtils } from '../game/YGOUtils';

export class ChangeCardAtkDefCommand extends BaseCommand {
    public baseType: string = "ChangeCardAtkDefCommand";
    private data: ChangeCardAtkDefCommandData;
    private oldAtk: number;
    private oldDef: number;

    constructor(data: ChangeCardAtkDefCommandData) {
        super();
        this.type = "Change Card Atk Def";
        this.data = data;
        this.oldAtk = 0;
        this.oldDef = 0;
    }

    override exec(): void {
        const card = this.YGO.state.getCardById(this.data.id, this.data.originZone)!;

        this.oldAtk = card.currentAtk;
        this.oldDef = card.currentDef;

        if (YGOUtils.isNumeric(this.data.atk)) {
            card.currentAtk = Number(this.data.atk);
        }

        if (YGOUtils.isNumeric(this.data.def)) {
            card.currentDef = Number(this.data.def);
        }

        this.YGO.duelLog.dispatch<YGODuelEvents.ChangeCardAtkDef>({
            player: this.data.player,
            commandId: this.getCommandId(),
            type: YGODuelEvents.LogType.ChangeCardAtkDef,
            id: this.data.id,
            originZone: this.data.originZone,
            atk: card.currentAtk,
            def: card.currentDef,
            oldAtk: this.oldAtk,
            oldDef: this.oldDef,
        });
    }

    override undo(): void {
        const card = this.YGO.state.getCardById(this.data.id, this.data.originZone);
        card.currentAtk = this.oldAtk;
        card.currentDef = this.oldDef;
    }
}